.App {
    background-color: var(--background-color);
    transition: background-color 0.25s ease-in-out;
}
#navbarCollapse {
    background-color: var(--background-color);
    color: var(--primary-text-color);
}
h1,
h2,
h3,
h4,
h5,h6 {
    color: var(--primary-text-color);
}

Link,span,a,p,i,ul,li {
    color: var(--primary-text-color);
}

